import { Molecule, SmilesElement, Vertex } from "../types/molecule.types";
import { DrawerConfig } from "../types/drawer.interface";
import { SmilesDrawerConfig } from "../types/smilesDrawer.types";
import SmilesDrawer from "./drawers/smilesDrawer";
import moleculeStructureService from "./molecule/molecule.structure.service";
class SmilesDrawerService {
    setVerticesInMolecule(molecule: Molecule, smilesDrawer: SmilesDrawer) {
        moleculeStructureService.setVerticesInSmilesElements(
            molecule.smilesElements!,
            smilesDrawer,this.findGVertex, this.createVertex
        );
        molecule.vertices =
            moleculeStructureService.getVerticesFromMolecule(molecule); //smilesDrawer.getOriginalVerticesFromExternalDrawer();
    }

    // this function is passed to other functions from moleculeStructureService
    private findGVertex = (gVertices: any[], smilesElement: SmilesElement) => {
        // in extendedSmilesDrawer, smilesElementIndex is the index where that element start in the in the smiles string (chars). The same for smilesElement.smilesIndex
        return gVertices.find(
            (v: any) => v.value.smilesElementIndex === smilesElement.smilesIndex
        );
    };

    private createVertex = (
        smilesElement: SmilesElement,
        gVertex: any
    ): Vertex | null => {
        // a gVertex comes from the drawer (specific and may be different depending on the drawer) while a vertex is a generic structure that stores position, index, smilesElement, and hover state.

        if (gVertex == null) return null;
        const atomIndex = gVertex.value.smilesElementIndex;
        //explicitly, because we don't have type of gVertex
        const position = { x: gVertex.position.x*2, y: gVertex.position.y*2 };
        return {
            atomIndex,
            position,
            atomSmilesElement: smilesElement,
            hover: false,
        };
    };

    public getConfig(config: DrawerConfig): SmilesDrawerConfig {
        const { width, height } = config;
        let structureColor: string | undefined = config.structureColor;
        if (config.drawerType.indexOf("black") === -1) {
            // the drawer should use the color palette: a different color per atom.
            structureColor =  undefined;
        }

        return {
            width: width,
            height: height,
            terminalCarbons: true,
            compactDrawing: false,
            explicitHydrogens: false,
            debug: false,
            theme: "light",
            themes: {
                light: {
                    C: "#222222",
                    O: "#e74c3c",
                    N: "#3498db",
                    F: "#27ae60",
                    CL: "#16a085",
                    BR: "#d35400",
                    I: "#8e44ad",
                    P: "#d35400",
                    S: "#f1c40f",
                    B: "#e67e22",
                    SI: "#e67e22",
                    H: "#222222",
                    BACKGROUND: "#ffffff",
                    numerical: false,    
                    domain: [],  // we dont color anything with extendedSmilesDrawer anymore             
                    colors: [], // we won't color the structure according to scores in the extendedSmilesDrawer
                    heatmap: false, // neither will we draw the heatmap
                    structureColor, // but we want it to draw the structure in black or so
                },
            },
        };
    }
}

export default new SmilesDrawerService();
