import { Molecule } from "../../types/molecule.types";
import { Drawer, DrawerConfig, DrawerType } from "../../types/drawer.interface";
import moleculeStructureService from "../molecule/molecule.structure.service";
import * as ExtendedSmilesDrawer from "extended-smiles-drawer";
import smilesDrawerService from "../smilesDrawer.service";
import rdkitServices from "../rdkit.service";

export default class SmilesDrawer implements Drawer {
    drawer: any = null;
    molecule: Molecule;
    type:DrawerType;

    constructor(canvasID: string, molecule: Molecule, config: DrawerConfig) {
        // parse smiles and get the graph
        // console.log("SmilesDrawer", config);      
        this.type = config.drawerType;       
        
        this.drawer = this.createSmilesDrawer(canvasID, config, molecule);
        this.updateMoleculeWithVertices(molecule);
        this.molecule = moleculeStructureService.cloneMolecule(molecule);
    }

    createSmilesDrawer = (divID: any, config: DrawerConfig, molecule: Molecule) => {
        const smilesDrawerConfig = smilesDrawerService.getConfig(config);
        return new ExtendedSmilesDrawer.Drawer(divID, molecule.sequence, smilesDrawerConfig);
    };

    updateMoleculeWithVertices(molecule: Molecule) {
        smilesDrawerService.setVerticesInMolecule(molecule, this);
    }

    updateMoleculeWithOldDrawerVertices(molecule: Molecule) {
        this.updateMoleculeWithVertices(molecule);
        this.molecule = moleculeStructureService.cloneMolecule(molecule);
    }

    draw = (div: HTMLDivElement) => {
        // clearDivChildren(div);
        console.warn("smilesDrawer.draw() is not set up to redraw");
    };

    getMolecule = () => {
        return this.molecule;
    };

    getOriginalVerticesFromExternalDrawer = (): any[] => {
        return this.drawer.getVertices();
    };
}
