import { DrawerConfig } from "../../types/drawer.interface";
import { Molecule } from "../../types/molecule.types";
import RDKitDrawer from "./rdkitDrawer";
import SmilesDrawer from "./smilesDrawer";

class DrawerService {
    public createDrawer(
        id: string,
        molecule: Molecule,
        drawerConfig: DrawerConfig
    ) {
        // console.log("Structure: creating new drawer ", drawerConfig.drawerType);
        if(drawerConfig.drawerType.indexOf("smilesDrawer")!==-1)
        return new SmilesDrawer(
            id,
            molecule,
            drawerConfig
        );

        if(drawerConfig.drawerType.indexOf("RDKitDrawer")!==-1)
        return new RDKitDrawer(
            id,
            molecule,
            drawerConfig
        );
    }
}

export default new DrawerService();
