import React, { useEffect, useRef } from "react";
import { StructureViewConfig } from "../../../../types/structure.types";
import { Molecule, SmilesElement, Vertex } from "../../../../types/molecule.types";
import { clearDivChildren, equalArrays, isEmptyNullUndefined } from "../../../../util";
import { color, rgb } from "d3-color";

interface Props {
    // hoverSmilesElements: SmilesElement[];
    hoverVertices: Vertex[];
    config: StructureViewConfig;
    scaleResolution: number;
}
interface State {}

function Highlight(props: Props) {
    const didMountRef = useRef(false);
    const divRef = React.createRef<HTMLDivElement>();

    const { config, hoverVertices, scaleResolution } = props;

    useEffect(() => {
        if (didMountRef.current && divRef.current) {
            drawCanvas(divRef.current, config, hoverVertices, scaleResolution);
        } else didMountRef.current = true;
    });

    if (isEmptyNullUndefined(hoverVertices) == null) return null;

    return (
        <div
            ref={divRef}
            className="Highlight"
            style={{ width: config.width, height: config.height, position: "absolute", top: "0px", left: "0px", zIndex: 1 }}
        ></div>
    );
}

function areEqual(prevProps: Props, nextProps: Props) {
    if (isEmptyNullUndefined(prevProps.hoverVertices) && isEmptyNullUndefined(nextProps.hoverVertices)) {
        return true;
    }
    if (!equalArrays(prevProps.hoverVertices, nextProps.hoverVertices)) return false;
    if (!equalArrays(prevProps.config.gradient.palette.colors, nextProps.config.gradient.palette.colors)) return false;

    return true;
}
export default React.memo(Highlight, areEqual);

const drawCircle = (ctx: CanvasRenderingContext2D, vertex: Vertex, color: string, scaleResolution: number) => {
    if (vertex && ctx) {
        ctx.fillStyle = color;
        ctx.shadowBlur = 7*scaleResolution;
        ctx.shadowColor = color;
        ctx.strokeStyle = "gray";
        ctx.lineWidth = 3*scaleResolution;
        ctx.beginPath();
        ctx.arc(vertex.position.x, vertex.position.y, 12*scaleResolution, 0, 2 * Math.PI); //TODO if RDKit Drawer, it should get the radius from bondLength
        ctx.fill();
        ctx.stroke();
    }
};

const drawCanvas = (target: HTMLDivElement, config: StructureViewConfig, hoverVertices: Vertex[], scaleResolution: number) => {
    const canvas = document.createElement("canvas");
    canvas.width = config.width*scaleResolution;
    canvas.height = config.height*scaleResolution;
    canvas.style.width = `${config.width}px`;
    canvas.style.height = `${config.height}px`;

    const colors = config.gradient.palette.colors;
    hoverVertices.forEach((vertex) => {
        const ctx = canvas.getContext("2d");
        // console.log("color", colors, vertex?.atomSmilesElement.score, color(getColor(colors, vertex!.atomSmilesElement.score)));
        if (ctx && vertex) {
            const c = color(getColor(colors, vertex.atomSmilesElement.score));
            c!.opacity = 0.6;
            drawCircle(ctx, vertex, c!.formatRgb(),scaleResolution);
        }
    });
    clearDivChildren(target);
    target.append(canvas);
};

const getColor = (colors: string[], score: number) => {
    if (score < 0) {
        return colors[0];
    } else {
        return colors[colors.length - 1];
    }
};
